import React from 'react'
import './Content.css'
import Card from '../Card/Card'


export default function Content() {
    let cards = []
    for (let i = 0; i < 8; i++) {
        cards.push(
            <Card key={i} />
        )
    }

    return (<div className="container-fluid">
        <div className="card-deck row justify-content-center my-5">
            {cards}
        </div>
    </div>)
}
