import React from 'react'
import './Movie.css'
import image from './image.jpg'

export default function Movie(props) {
    console.log(props)
    return (
        <div className="container row">
            <img src={image} className="rounded col-xl-4 offset-md-1" alt="movie-post" />
            <div className="col-xl-4 offset-xl-1">
                <h1>Ad Astra</h1>
                <hr />
                <strong> Description: </strong>
                <p>The near future, a time when both hope and hardships drive humanity to look to the stars and beyond.
                While a mysterious phenomenon menaces to
                destroy life on planet Earth, astronaut Roy McBride undertakes a mission across the immensity
                of space and its many perils to uncover the truth about a lost expedition that decades before boldly
                faced emptiness and silence in search of
                the unknown.</p>
                <hr />

                <div><strong>Generes: </strong>
                    <span className="badge badge-pill badge-success">Success</span>
                    <span className="badge badge-pill badge-danger">Danger</span>
                    <span className="badge badge-pill badge-warning">Warning</span>
                    <span className="badge badge-pill badge-info">Info</span>
                </div>

                <hr />

                <div><strong>Rate: </strong><i>7/10</i></div>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star"></span>
                <span className="fa fa-star"></span>
                <span className="fa fa-star"></span>
                <span className="fa fa-star"></span>
                <hr />
                <div>
                    <strong> Trailer: </strong>
                </div>
                <div className="trailer">
                
                    <iframe frameBorder="0" allowFullScreen="1"
                        title="YouTube video player"
                        src="https://www.youtube.com/embed/BdJKm16Co6M?controls=1"
                        >
                    </iframe>
                </div>
            </div>
        </div>
    )
}

